<template>
  <div id="login-body">
    <div class="right">
      <h1>{{ msg }}</h1>
    </div>
    <el-form id="login-form"
             label-position="right"
             label-width="80px"
             :model="ruleForm"
             :rules="rules"
             ref="ruleForm"
    >
      <el-form-item label="" prop="name">
        <el-input v-model="ruleForm.name"></el-input>
      </el-form-item>
      <el-form-item label="" prop="pass">
        <el-input type="password" v-model="ruleForm.password" autocomplete="off"></el-input>
      </el-form-item>
    </el-form>
    <el-row>
      <el-button type="primary" round @click="login"></el-button>
    </el-row>
  </div>
</template>

<script>
  export default {
    name: "Login",
    data() {
      var validateName = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else {
          /*if (this.ruleForm.name !== '') {
            this.$refs.ruleForm.validateField('checkPass');
          }
          callback();*/
        }
      };
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else {
          /*if (this.ruleForm.password !== '') {
            this.$refs.ruleForm.validateField('checkPass');
          }
          callback();*/
        }
      };
      return {
        msg: '',
        ruleForm: {
          name: '',
          password: ''
        },
        rules: {
          name: [
            { required: true, message: '', trigger: 'blur' },
          ],
          password: [
            { required: true, message: '', trigger: 'blur' },
          ]
        }
      }
    },
    methods:{
      login() {
        if (this.ruleForm.name === 'test' && this.ruleForm.password === 'test') {
          this.$router.push('/index/projectView');
        } else {
          this.$message({
            message: "",
            type: "error",
            showClose: true,
            duration: "2000"
          });
        }
      }
    }
  }
</script>

<style scoped>

  .right h1, h2 {
    font-weight: normal;
    font-size: 30px;
  }

  #login-body {
    padding-left: 40%;
    padding-right: 40%;
  }

  #login-form {

  }

</style>



